import BigCalendar from "react-big-calendar";
import moment from "moment";

import "react-big-calendar/lib/css/react-big-calendar.css";
// Setup the localizer by providing the moment (or globalize) Object
// to the correct localizer.
moment.locale("en-US");
BigCalendar.momentLocalizer(moment);

const myEventsList = [
  {
    id: 6,
    title: "Meeting",
    start: new Date(2019, 3, 12, 10, 30, 0, 0),
    end: new Date(2019, 3, 12, 12, 30, 0, 0),
    desc: "Pre-meeting meeting, to prepare for the meeting"
  },
  {
    id: 7,
    title: "Interview",
    start: new Date(2019, 3, 12, 1, 30, 0, 0),
    end: new Date(2019, 3, 12, 2, 30, 0, 0),
    desc: "Pre-meeting meeting, to prepare for the meeting"
  },
  {
    id: 8,
    title: "Work Out",
    start: new Date(2019, 3, 12, 2, 30, 0, 0),
    end: new Date(2019, 3, 12, 4, 30, 0, 0),
    desc: "Pre-meeting meeting, to prepare for the meeting"
  }
];

const EventCalendar = props => (
  <div style={{ height: 700 }}>
    <BigCalendar
      events={myEventsList}
      step={60}
      view="week"
      views={["week"]}
      min={new Date(2008, 0, 1, 8, 0)} // 8.00 AM
      max={new Date(2008, 0, 1, 17, 0)} // Max will be 6.00 PM!
      date={new Date(2018, 0, 1)}
    />
  </div>
);

export default EventCalendar;
